
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: 'hello',
data () {
return {
author: '',
digest: '',
title: '',
article: '',
dialogVisible: false,
catList: [],
show: false,
cityName:'',
weather:'',
plainList: ['佛山', '广州','深圳','汕头'],
value1: '佛山'
}
},
directives: {
TransferDom
},
components: {
XDialog,
XButton,
Group,
XSwitch,
Selector,
GroupTitle,
Cell
},
methods: {
getShit() {
let _this = this;
this.$http.get('http://localhost:3000/api')
.then(function(res){
res.data.hello.forEach(cat=>{
_this.catList.push(cat);
})
})
.catch(function(error){
console.log(error)
})
},
getArticle() {
let _this = this;

this.$http.get('https://interface.meiriyiwen.com/article/random?dev=1')


.then(function(res){
_this.article = res.data.data.content;
_this.title = res.data.data.title;
_this.author = res.data.data.author;
_this.digest = res.data.data.digest;
})
},
getWeather(val) {
console.log(val)
let _this  = this;





this.$http.get('http://tj.nineton.cn/Heart/index/all?city='+val)


.then(function(res){
_this.weather = res.data.weather[0].future[0].text;
_this.cityName = res.data.weather[0].city_name;
})
},
changeArticle() {
this.getArticle();
},
onChange (val) {
switch(val)
{
case '佛山':
this.getWeather('CHGD070000');
break;
case '广州':
this.getWeather('CHGD000000');
break;
case '深圳':
this.getWeather('CHGD050000');
break;
case '汕头':
this.getWeather('CHGD040000');
break;
default:
this.getWeather('CHGD070000');
}

}
},
mounted() {

this.getArticle();
this.getWeather(this.value1);
}
}



